*{
    padding:0px;
    margin: 0px;
    box-sizing: border-box;
}

body{
    background-color: rgb(1, 1, 24);
    color: white;
}

h3{
    font-size: 2rem;
    font-family: sans-serif;
    text-transform: capitalize;
    text-align: center;
    width: 40%;
    margin: auto;
    line-height: 40px;
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.container{
    display: flex;
    margin-top: 2rem;
}

.wraper{
    display: flex;
    gap: 25px;
    margin-top: 1rem;
    position: relative;
    left: 30%;
}

.box{
    width: 255px;
    height: 130px;
    border: 2px solid rgb(2, 2, 34);
    display:flex;
    justify-content:center;
    align-items:center;
}

.trincome{
    color: green;
}

.trexpensive{
    color: red;
}
.trbalance{
    color: green;
}

 h4{
    font-size: 2rem;
    font-family: sans-serif;
    font-weight: bold;
 }

.box > div p{
    margin-top: 1rem;
    font-family: sans-serif;
    text-transform: capitalize;
    letter-spacing: 1px;
}

table{
    padding: 2rem;
    width: 60%;
    border-collapse: collapse;
    position: relative;
    left: 30%;
    border: 2px solid rgb(2, 2, 34);
}

strong{
  color: green;
}

tr:nth-child(even){
    background-color: rgb(2, 2, 34);
}

th{
    text-align: left;
    padding: 1rem;
    font-size: 20px;
    text-transform: capitalize;
    font-weight: bold;
}

td{
    font-family: sans-serif;
    text-transform: capitalize;
    padding: 13px;
    text-align: left;
    font-size: 17px;
}

.income{
    color: green;
    font-weight: bold;
    font-family: sans-serif;
}
.expensive{
     color: red;
     font-family: sans-serif;
     font-weight: bold;
}

.sidebar{
    
    width:250px;
    height: 100px;
    position: absolute;
    top:9rem;
    left: 8rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

ul li{
    padding: 10px;
    font-size: 20px;
    font-family: sans-serif;
    text-transform: capitalize;
    color: white;
    list-style: none;
    cursor: pointer;
    background-color: rgb(2, 2, 33);
    border: 1px solid green;
    border-radius: 10px;
    margin-bottom: 10px;
}

.btncontainer{
    display: flex;
    gap:5px;
}

.edit{
    padding: 6px;
    font-family: sans-serif;
    text-transform: capitalize;
    font-weight: bold;
    outline: none;
    border: none;
    background-color: green;
    color: white;
    border-radius: 5px;
}

.delete{
    padding: 6px;
    font-family: sans-serif;
    text-transform: capitalize;
    font-weight: bold;
    outline: none;
    border: none;
    background-color: red;
    color: white;
    border-radius: 5px;
}

.incomeform{
    background-color: rgb(0, 128, 0,0.40);
    transition: 1s;
    min-height: 800px;
    width:100%;
    position: absolute;
    z-index: 2;
    display: block;
}

.inputcontainer{
     width:30%;
     background-color:rgb(2, 2, 34);
     padding: 2rem;
     position: absolute;
     top:50%;
     left:50%;
     transform: translate(-50%, -50%);
}

.activeincomeform{
      display: none;
}

.activeexpform{
    display: none;
}

.inputcontainer input{
    width: 100%;
    padding: 1rem;
    font-family: sans-serif;
    text-transform: capitalize;
    margin-top: 5px;
    border: none;
    outline: none;
    font-size: 16px;
    background-color: rgb(1, 1, 24);
    color: white;
}

.inputcontainer button{
    width: 100%;
    background-color: green;
    font-size: 18px;
    text-transform: capitalize;
    font-weight: bold;
    color: white;
    border: none;
    outline: none;
    padding: 14px;
    margin-top: 5px;
}

.inputcontainer h2{
     font-size: 2rem;
     font-family: sans-serif;
     text-transform: capitalize;
     font-weight: bold;
     margin-bottom: 1rem;
}

.cancelbtn{
     font-size: 18px;
     font-family: sans-serif;
     text-transform: capitalize;
     font-weight: bold;
     color: green;
     position: absolute;
     right: 2rem;
     top: 10px;
     cursor: pointer;
}



